.page {
  height: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  white-space: nowrap;
  border: 0;
}

.title {
  font-weight: 700;
  line-height: 30px;
}

.button {
  min-height: 52px;
  font-weight: 700;
  line-height: 20px;
  color: $black-blue-text;
  text-transform: uppercase;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid $black-blue-text;
}
