@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/open-sans-v29-latin-regular.woff") format("woff"), url("../fonts/open-sans-v29-latin-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/open-sans-v29-latin-700.woff2") format("woff"), url("../fonts/open-sans-v29-latin-700.woff2") format("woff2");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul[class] {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

.page {
  height: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  white-space: nowrap;
  border: 0;
}

.title {
  font-weight: 700;
  line-height: 30px;
}

.button {
  min-height: 52px;
  font-weight: 700;
  line-height: 20px;
  color: #444444;
  text-transform: uppercase;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid #444444;
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "Open Sans";
}

.page-header {
  flex: 0 0 auto;
}

.page-main {
  flex: 1 1 auto;
}

.page-footer {
  flex: 0 0 auto;
}

.page-header {
  position: relative;
  display: grid;
  grid-template-rows: 79px repeat(2, auto);
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}

.page-header__logo-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-header__toggle {
  position: absolute;
  top: 2.1em;
  right: 2.1em;
  width: 20px;
  height: 2px;
  background-color: #000000;
  border: none;
}
.page-header__toggle::after {
  content: "";
}
.page-header__toggle::before {
  content: "";
}

.site-list {
  display: flex;
  flex-wrap: wrap;
}

.site-list__item {
  width: 100%;
  height: 79px;
}

.site-list__link {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-left: 76px;
}

.user-list {
  display: flex;
  flex-wrap: wrap;
}

.user-list__item {
  width: 100%;
  height: 79px;
}

.user-list__link {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-left: 76px;
}

.user-list__svg {
  position: absolute;
  left: 2em;
  fill: #000000;
}

.main-nav {
  margin-bottom: 15px;
}
.main-nav a {
  font-weight: 700;
  color: #000000;
}

.main-nav__list li {
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}

.main-header {
  display: block;
  width: 100%;
  height: 506px;
  padding-top: 90px;
  margin-bottom: 31px;
  background-image: url("../../img/hero-mobile@2x 1.png");
  background-repeat: no-repeat;
  background-position: 50% 0;
}

.main-header__title {
  margin-bottom: 120px;
  font-size: 27px;
  text-align: center;
}

.btn-list {
  display: flex;
  flex-wrap: wrap;
}

.btn-list__item {
  width: 100%;
}

.main-header__button {
  position: relative;
  display: block;
  width: 100%;
  height: 103px;
  font-size: 19px;
  font-weight: 700;
  line-height: 24px;
  color: #ffffff;
  cursor: pointer;
  border: none;
}

.btn-left {
  background-color: #63d1bb;
}

.btn-right {
  background-color: #56c3b2;
}

.product-cards {
  position: relative;
  padding: 0 30px;
  margin-bottom: 31px;
}
.product-cards::after {
  position: absolute;
  left: 0;
  display: block;
  width: 320px;
  height: 6px;
  content: "";
  background-image: url("../../img/zigzag-line-desktop.svg");
  background-position: center;
}

.product-cards__content {
  padding-bottom: 36px;
}

.product-card {
  display: grid;
  row-gap: 28px;
}

.product-card__title {
  font-size: 25px;
}

.product-card__text {
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}

.product-card__category {
  font-size: 17px;
  font-weight: 700;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
}

.product-card__cart-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-height: 110px;
}

.product-card__price {
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  color: #444444;
}

.product-card__button {
  width: 100%;
  font-size: 18px;
}

.product-card__img {
  grid-row-start: 4;
  min-width: 100%;
}

.property-item {
  display: flex;
  align-items: center;
  height: 40px;
  font-size: 15px;
  line-height: 20px;
  color: #444444;
  border-bottom: 1px solid #f1f1f1;
}

.property-item__key {
  flex-basis: 97px;
}

.about {
  padding: 0 30px;
  margin-bottom: 43px;
}

.about__title {
  font-size: 25px;
}

.about__list {
  display: grid;
  gap: 15px;
  margin-top: 28px;
  font-size: 16px;
  font-weight: 700;
  line-height: 118%;
}

.about__item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100px;
}
.about__item::before {
  background-repeat: no-repeat;
  background-position: left top;
}

.material::before {
  content: url("../../img/icon-1.svg");
}

.style::before {
  content: url("../../img/icon-3.svg");
}

.like::before {
  content: url("../../img/icon-5.svg");
}

.handmade::before {
  content: url("../../img/icon-2.svg");
}

.support::before {
  content: url("../../img/icon-4.svg");
}

.package::before {
  content: url("../../img/icon-6.svg");
}

.reviews {
  position: relative;
  padding: 0 30px;
  margin-bottom: 38px;
}
.reviews::after {
  position: absolute;
  left: 0;
  display: block;
  width: 320px;
  height: 6px;
  content: "";
  background-image: url("../../img/zigzag-line-desktop.svg");
  background-position: center;
}

.reviews__title {
  font-size: 25px;
}

.reviews__list {
  margin-top: 15px;
  margin-bottom: 30px;
}

.reviews__content-wrapper {
  padding-bottom: 45px;
}

.reviews__button {
  width: 100%;
  font-size: 18px;
}

.reviews__content {
  margin-bottom: 31px;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
}

.reviews__controls {
  display: flex;
  width: 100%;
  height: 52px;
  margin-bottom: 38px;
}

.reviews__control {
  width: 50%;
  height: 100%;
}

.reviews__author-name {
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
}

.reviews__author-profile {
  font-size: 17px;
  font-weight: 400;
  line-height: 30px;
  color: #444444;
}

.contacts {
  padding: 0 30px;
  margin-bottom: 39px;
}

.contacts__wrapper {
  padding-bottom: 39px;
}

.contacts__title {
  margin-bottom: 28px;
  font-size: 18px;
}

.contacts__list {
  margin-bottom: 62px;
}

.contacts__item {
  display: flex;
  width: 100%;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  color: #444444;
}

.contacts__type {
  flex-basis: 33%;
}

.contacts__button {
  width: 100%;
}

.contacts__email {
  color: inherit;
}

.contacts__map {
  margin-bottom: 42px;
  margin-left: -30px;
}

.slider__item {
  display: none;
}

.slider__item--active {
  display: block;
}

.slider__button-back,
.slider__button-forward {
  min-width: 50px;
  min-height: 26px;
  padding: 0;
  margin: 0;
  cursor: pointer;
  background: center no-repeat url("../../img/arrow-right.svg");
  border: 1px solid #f1f1f1;
}

.slider__button-back {
  transform: rotate(180deg);
}

.page-footer {
  display: flex;
  flex-wrap: wrap;
  padding: 0 30px;
  background-color: #f1f1f1;
}

.page-footer > * {
  flex-basis: 100%;
  height: 80px;
}

.page-footer > *:not(:last-child) {
  border-bottom: 1px solid #e5e5e5;
}

.copyright__link {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 100%;
}

.social__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 200px;
  height: 100%;
  margin: 0 auto;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.catalog-header {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 177px;
  background-color: #f1f1f1;
}

.catalog-header__title {
  font-size: 27px;
}

.catalog-cards {
  padding: 0 30px;
  margin-bottom: 28px;
}

.catalog-cards__list {
  display: flex;
  flex-wrap: wrap;
}

.catalog-cards__item {
  margin-top: 30px;
}

.catalog-card {
  display: grid;
}

.catalog-card__title {
  margin-top: 27px;
  margin-bottom: 6px;
  margin-left: 10%;
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
}

.catalog-card__img {
  grid-row-start: 1;
}

.catalog-card__params {
  margin-bottom: 17px;
  margin-left: 10%;
  font-size: 13px;
  line-height: 30px;
  color: #444444;
}

.card-cart {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 75px;
  margin: 0 8%;
  border-top: 1px solid #f1f1f1;
}
.card-cart::after {
  position: absolute;
  bottom: 0;
  left: -27px;
  width: 320px;
  height: 1px;
  content: "";
  background-color: #f1f1f1;
}

.card-cart__price {
  font-size: 15px;
  font-weight: 700;
  line-height: 24px;
}

.card-cart__btn {
  width: 25%;
  height: 50%;
  text-align: right;
  cursor: pointer;
  background-color: inherit;
  border: none;
}

.catalog-info {
  padding: 0 30px;
  padding-bottom: 35px;
  margin-bottom: 56px;
  border-bottom: 1px solid #f1f1f1;
}

.catalog-info__content-wrapper {
  margin-left: 34px;
}

.catalog-info__title {
  display: flex;
  align-items: center;
  margin-bottom: 26px;
  font-size: 17px;
  line-height: 24px;
}
.catalog-info__title::before {
  margin-right: 27px;
  content: url("../../img/video.svg");
}

.catalog-info__text {
  margin-bottom: 41px;
  font-size: 13px;
  line-height: 24px;
  color: #444444;
}

.catalog-info__order {
  margin-right: 34px;
}

.catalog-info__video-wrapper {
  position: relative;
  margin-left: -34px;
}

.catalog-info__play-btn {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 101px;
  height: 77px;
  padding: 0;
  margin: auto;
  overflow: auto;
  cursor: pointer;
  background-color: #63d1bb;
  border: 5px solid #ffffff;
  border-radius: 20%;
}
.catalog-info__play-btn::before {
  position: absolute;
  top: 20%;
  right: 5%;
  content: "";
  border: 20px solid transparent;
  border-left: 35px solid #ffffff;
}